import React from "react";
import "./index.css";
import { Dropdown, Menu } from "semantic-ui-react";
import { Link, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { logout } from "../store/actions/loginAction";

const HeaderFunction = props => {
  if (props.token === null) {
    return (
      <div className="headerTop">
        <Menu color="black">
          <Menu.Item name="home">
            <Link to="/">Home</Link>
          </Menu.Item>
          <Menu.Item name="myHome">
            <Link to="/login">Mein Zuhause</Link>
          </Menu.Item>
          <Menu.Item name="login">
            <Link to="/login">Anmelden</Link>
          </Menu.Item>
        </Menu>
      </div>
    );
  } else {
    return (
      <div className="headerTop">
        <Menu color="black">
          <Menu.Item name="home">
            <Link to="/">Home</Link>
          </Menu.Item>
          <Menu.Item name="myHome">
            <Link to="/summary">Mein Zuhause</Link>
          </Menu.Item>
          <Dropdown icon="user" item text="MeinBenutzer">
            <Dropdown.Menu>
              <Dropdown.Item onClick={props.logOut}>Abmelden</Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </Menu>
      </div>
    );
  }
};

class HeaderComp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  userLogout = () => {
    this.props.dispatch(logout());
    this.props.history.push("/");
  };

  render() {
    return (
      <div>
        <HeaderFunction token={this.props.token} logOut={this.userLogout} />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    token: state.loginReducer.token
  };
};

export default connect(mapStateToProps)(withRouter(HeaderComp));
